.navbar {
  background-color: lightgray;

  &-content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-top: 1rem;
    padding-bottom: 1rem;
  }

  &-button {
    border: 0;
    background-color: transparent;
    margin-left: auto;
    cursor: pointer;
    @include tablet {
      display: none;
    }
  }

  &-list {
    display: none;
    margin-left: auto;

    & > li + li {
      margin-top: 1rem;
    }

    @include tablet {
      display: flex;
      flex-direction: row;

      & > li + li {
        margin-top: 0;
        margin-left: 1rem;
      }
    }
  }
}

.logo {
  overflow: hidden;
  white-space: nowrap;
}

.header {
  background-color: lightsteelblue;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  & > h3 {
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem;

    @include tablet {
      font-size: 2.5rem;
    }
  }
}

.mobile-menu {
  &-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    transition: all .3s;
  }
  &-content {
    position: fixed;
    top: 0;
    right: -1000px;
    background: #fff;
    width: 300px;
    height: 100vh;
    padding: 1rem 3rem;
    z-index: 2;
    transition: right .5s;
  }
  &-slideIn {
    right: 0;
  }
  &-closeBtn {
    border: 0;
    background-color: #fff;
    position: absolute;
    top: 2%;
    right: 2%;
    cursor: pointer;
  }
  &-list {
    & > li + li {
      margin-top: 2rem;
    }
    & > li > a {
      font-size: 1.5rem;
      padding: 0.5rem 0;
    }
  }
}

.d-none {
  display: none;
}

.ml-1{
  margin-left: .5rem;
}
.ml-2{
  margin-left: 1rem;
}